import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import styles from './ModelList.css';
import DataTable from 'react-data-table-component';
import CustomizedBreadcrumbs from '../../components/breadcumb/breadcumb';
import SideBar from '../../components/sidebar/sidebar';
import CustomizedSnackbars from '../../components/snackbar/snackbar';
import CenteredGrid from '../../components/gridList/gridList';
import { Typography } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle'



class ModelList extends Component {
    constructor() {
        super();
        this.state = {
            sidebarClick: true,
            model: [],
            modelOpen: false,
            model_name: '',
            message_type: '',
            create_status: false,
            message_content: ''

            
            
        }
        
        
    }

    componentDidMount() {
        this.loadModel(); 

        
    }

   

    loadModel() {
        
        axios({ method: 'get', url: '/admin/deviceprofile/model/list' }).then((resp) => {
            this.setState({model: resp.data.models});
        }).catch((err) => {
            if (err) {
                alert("Sorry some internal error occured! Please try after some time");
            }
        })
    }

    toggleSidebar() {

        this.setState({ sidebarClick: !this.state.sidebarClick });
    }

    openAddModel() {
            this.setState({modelOpen: true});
    }

    closeModal() {
        this.setState({modelOpen: false});
    }

    changeModel(event) {
        this.setState({model_name: event.target.value});
    }

    saveModel() {
        if (this.state.model_name == "") {
            this.setState({
                message_type: 'error', 
                message_content: 'Model name cannot be blank',
                create_status: true});
            setTimeout(() => {this.setState({create_status: false})}, 2000)
            return false;
             
        }
        let data = {modelName: this.state.model_name};
        axios.post('/admin/deviceprofile/model', data).then((resp) => {
            this.setState({modelOpen: false, 
                modelName: '', 
                message_type: 'success', 
                message_content: 'Model created successfully',
                create_status: true});
            this.loadModel();
        }).catch((err) => {
            console.log("Error hapend")
            this.setState({modelOpen: false, 
                modelName: '', 
                message_type: 'error', 
                message_content: 'Some problem occured! Please try after some time',
                create_status: true});
        });
        setTimeout(() => {this.setState({create_status: false})}, 2000)
    }
    
   

    render() {



        let inlineStyle = {
            paddingLeft: '0px'
        }

        if (this.state.sidebarClick) {
            inlineStyle.paddingLeft = '300px'
        } else {
            inlineStyle.paddingLeft = '0px'
        }

        let site_list = [];


        return (
            <div>
                <CustomizedSnackbars open={this.state.create_status} type={this.state.message_type} message={this.state.message_content} />
                <SideBar sidebarClick={this.state.sidebarClick} onClick={this.toggleSidebar.bind(this)} />
                <main className={styles.pageContent} style={inlineStyle}>
                    <div className="container-fluid">
                        <Typography variant="h5">Model List</Typography>

                        <Button variant="outlined" color="primary" onClick={this.openAddModel.bind(this)}>
                            Add Model
                        </Button>

                        <CenteredGrid models={this.state.model}/>

                        <Dialog open={this.state.modelOpen} onClose={this.closeModal.bind(this)} aria-labelledby="form-dialog-title">
                            <DialogTitle id="form-dialog-title">Model List</DialogTitle>
                            <DialogContent>
                                <DialogContentText>
                                    
                            </DialogContentText>
                                <TextField
                                    autoFocus
                                    margin="dense"
                                    id="name"
                                    label="Model Name"
                                    type="text"
                                    fullWidth
                                    onChange={this.changeModel.bind(this)}

                                />
                            </DialogContent>
                            <DialogActions>
                                <Button onClick={this.closeModal.bind(this)} color="primary">
                                    Cancel
                                </Button>
                                <Button onClick={this.saveModel.bind(this)} color="primary">
                                    Save
                                </Button>
                            </DialogActions>
                        </Dialog>


                    </div>
                </main>
            </div>


        )
    }



}


const mapStateToProps = state => {
    return state;
}


export default connect(mapStateToProps)(ModelList);


