// SideBar.js

import React, { Component } from "react";
import {Redirect} from 'react-router-dom';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';
import styles from './sidebar.css';
import axios from 'axios';


class SideBar extends Component {

  constructor(props) {
    super();
    this.state = {active_url: ''}
  }

  componentWillMount() {
     let arr = window.location.href.split('/');
     let active_url = arr[arr.length - 1];
     this.setState({active_url: active_url});
  }

  toggleSidebar() {
    if (typeof this.props.onClick === 'function') {
       this.props.onClick()
    }
    
  }

  logOut() {
    this.props.sidebarReducer.menus.map((e, i) => {
      if (e.active) {
        e.active = false;
      }
     });
    localStorage.removeItem('access_token');
  }

  

  render() {

    let getSession = localStorage.getItem('access_token');

    if (!getSession) {
        return <Redirect to="/login" />
    }
    
    let menuList = this.props.sidebarReducer.menus.map((e, i) => {
      return (
        <li key={i} className={[styles.sidebarDropdown, (e.active || e.link == '/'+this.state.active_url) ? styles.active : ''].join(' ')}>
          <Link to={e.link} onClick={() => {this.props.dispatch({type: 'TOGGLE_SUBMENU', index: i})}} className={(e.submenu.length == 0) ? styles.noAfter : ''}>
            <i className={e.icons}></i>
            <span>{e.name}</span>
          </Link>
          {e.submenu.length > 0 &&
            <div className={[styles.sidebarSubmenu, (e.active) ? styles.show : styles.hide].join(' ')}>
              <ul>
                {e.submenu.map((sub) => {
                  return (
                    <li key={sub.submenu_name} className={(sub.active ? styles.active: '')}  onClick={() => {this.props.dispatch({type: 'SELECT_SUBMENU', index: i})}}>
                      <Link to={sub.link}>
                          {sub.submenu_name}
                      </Link>
                    </li>
                  )
                })}

              </ul>
            </div>
          }
        </li>
      )
    })
    return (
      <div id="page-wrapper" className={[styles.chillerTheme, this.props.sidebarClick ? styles.toggled : ''].join(' ')}>
      
        <a id={styles.showSidebar} className="btn btn-sm btn-dark" href="#" onClick={this.toggleSidebar.bind(this)}>
          <i className="fas fa-bars"></i>
        </a>
        <nav id="sidebar" className={styles.sidebarWrapper}>
          <div className={styles.sidebarContent}>
            <div className={styles.sidebarBrand}>
              <a href="#">Smart Bag</a>
              <div id={styles.closeSidebar} onClick={this.toggleSidebar.bind(this)}>
                <i className="fas fa-times"></i>
              </div>
            </div>
            
            <div className={styles.sidebarHeader}>
              
              <div className={styles.userInfo}>
                <img
                    className="img-responsive img-rounded"
                    src="/img/volta.png"
                    alt="User picture"
                    height="60"
                    width="200"
                  />
              </div>
            </div>



            <div className={styles.sidebarMenu}>
              <ul>
                <li className={styles.headerMenu}>
                  <span>General</span>
                </li>
                {menuList}
              </ul>
            </div>
          </div>

          <div className={styles.sidebarFooter}>
            
            <Link onClick={this.logOut.bind(this)}>
              <i className="fa fa-power-off"></i>Logout
            </Link>
          </div>
        </nav>
      </div>
    );
  }
}

SideBar.propTypes = {
  onClick: PropTypes.func.isRequired,
  sidebarClick: PropTypes.bool.isRequired
}

const mapStateToProps = state => {
  return state;
}


export default connect(mapStateToProps)(SideBar);



