import React, { Component } from "react";
import { Redirect } from 'react-router-dom'
import Button from '@material-ui/core/Button';
import styles from "./LoginPage.css";

import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";

import axios from 'axios';

import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import { submitButton as SubmitButton } from "../../components/submit-button/submit-button";

class LoginPage extends Component {
  constructor() {
    super();
    this.state = { username: '', password: '', errorMessage: '' };
  }

  submitLogin() {
    let un = this.state.username;
    let pass = this.state.password;
    axios.post('http://54.162.17.115:3001/airpurifer/user/login', {username: un, password: pass}).then((resp) => {
      
      console.log("Response is", resp);
      localStorage.setItem('access_token', resp.data.tokens.accessToken);
      this.props.history.push('/device-list')
    })
  }

  updateValue(e) {
    let json = { ...this.state };
    json[e.target.name] = e.target.value;
    this.setState(json);
    this.setState({ errorMessage: '' });
  }

  render() {
    let errorMessage = this.state.errorMessage;
    let err = '';
    if (errorMessage !== '') {
      err = <span> {this.state.errorMessage}</span>
    }

    let validateLogin = localStorage.getItem('access_token');

    if (validateLogin) {
      return <Redirect to='/device-list' />
    }

    const style = {
      color: 'red'
    }
    return (
      <div className={styles.login}>

        <Container className={styles.container} component="main" maxWidth="xs">
          <CssBaseline />
          <form noValidate>
            <Typography component="h2" variant="h5" color="primary">
              <img src="/img/volta.png" />
            </Typography>
            <span style={style}>{err}</span>
            <Typography variant="h6" className={styles.typography}>
                Admin Login
            </Typography>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="username"
              autoComplete="email"
              autoFocus
              onChange={this.updateValue.bind(this)}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              onChange={this.updateValue.bind(this)}
            />
            {/* <SubmitButton text="Sign In" onClick={this.submitLogin.bind(this)} /> */}
            <Button
              type="button"
              fullWidth
              variant="contained"
              color="primary"
              onClick={this.submitLogin.bind(this)}
            >
              Submit
        </Button>
          </form>
        </Container>
      </div>
    );
  }
}

export default LoginPage;



