import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Select from '@material-ui/core/Select';

import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import axios from 'axios';
import CustomizedSnackbars from '../snackbar/snackbar';

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    margin: 0,
    fullWidth: true,
    display: 'flex',
    wrap: 'nowrap'
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  }
  
}));

export default function EditDeviceModal(props) {



  let [open, setOpen] =  React.useState(props);
  const [isStatus, setStatus] = React.useState(false);
  
  React.useEffect(() => {
    setOpen(props.open);
  }, [props.open])  

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    props.closeEditModal();
   setOpen(false);
  };



  const classes = useStyles();
  const [model, setModel] = React.useState('');

  React.useEffect(() => {
    setModel(props.device_data.model);
  }, [props.device_data.model])  

  const [device_id, setDeviceId] = React.useState('');

  React.useEffect(() => {
    setDeviceId(props.device_data.deviceId);
  }, [props.device_data.deviceId]) 

  const [macAddress, setMacAddress] = React.useState('');

  React.useEffect(() => {
    setMacAddress(props.device_data.macAddress);
  }, [props.device_data.macAddress]) 

  
  const handleChange = event => {
    setModel(event.target.value);
  };

  const handleDeviceIdChange = (e) => {
    (event.target.name == 'device_id') ? setDeviceId(event.target.value) : setMacAddress(event.target.value);
  }

  const registerDevice = (id, e) => {

    let data = {deviceId: device_id, macAddress: macAddress, model: model};
    console.log(id);
    axios.put('/admin/deviceprofile/'+id, data).then((resp) => {
          setStatus(true);
          setOpen(false);
          setTimeout(() => {
            setStatus(false);
          }, 2000);
    });

  }


  return (
    <div>
      <CustomizedSnackbars open={isStatus} type="success" message="Device updated successfully" />
     
      <Dialog open={props.open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Edit Device</DialogTitle>
        <DialogContent>
          
        <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Device id"
            type="mac_address"
            fullWidth
            name="device_id"
            value={device_id}
            onChange={handleDeviceIdChange}
          />

          <FormControl className={classes.formControl}>
                  <InputLabel id="demo-simple-select-label">Select Model</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={model}
                    onChange={handleChange}
                  >
                    {props.model && props.model.map((e) => <MenuItem value={e}>{e}</MenuItem>)}
                    
                    
                  </Select>
            </FormControl>

          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Device Mac Address"
            type="mac_address"
            name="mac_address"
            fullWidth
            onChange={handleDeviceIdChange}
            value={macAddress}
          />

          
          
          
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleClose} color="primary" onClick={registerDevice.bind(this, props.device_data.deviceId)}>
            Update
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}


