import React, {Component} from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Url from './component';
import {TitleComponent} from './Title';




class AppRoutes extends Component {
    

    
    render() {
        
        var arrayRoutes = [];
        const routes = Url.map((e, i) => {
            arrayRoutes.push(<Route key={i} exact path={e.url} component={e.component} />)
            return arrayRoutes;
        });
 
       
        return (
            <Router>
                {arrayRoutes}
            </Router>
        )
    }

}



export default AppRoutes;



