import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Select from '@material-ui/core/Select';

import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import axios from 'axios';
import CustomizedSnackbars from '../snackbar/snackbar';

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    margin: 0,
    fullWidth: true,
    display: 'flex',
    wrap: 'nowrap'
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export default function AddDeviceModal(props) {
  let [open, setOpen] =  React.useState(false);
  const [isStatus, setStatus] = React.useState(false);
  

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
   setOpen(false);
  };

 React.useEffect(() => {
   let status = props.isOpen;
   if (status) {
      setOpen(true);
   }
   
 }, [])

  const classes = useStyles();
  const [model, setModel] = React.useState('');
  const [device_id, setDeviceId] = React.useState('');
  const [macAddress, setMacAddress] = React.useState('');

  const handleChange = event => {
    setModel(event.target.value);
  };

  const handleDeviceIdChange = (e) => {
    (event.target.name == 'device_id') ? setDeviceId(event.target.value) : setMacAddress(event.target.value);
  }

  const registerDevice = (e) => {

    let data = {deviceId: device_id, macAddress: macAddress, model: model};
    axios.post('/admin/deviceprofile/register', data).then((resp) => {
          setStatus(true);
          setOpen(false);
          props.load_device();
          setTimeout(() => {
            setStatus(false);
          }, 500);
    });

  }


  return (
    <div>
      <CustomizedSnackbars open={isStatus} type="success" message="Device created successfully" />
      <Button variant="outlined" color="primary" onClick={handleClickOpen}>
            Add Device
      </Button>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Add Device</DialogTitle>
        <DialogContent>
          
        <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Device id"
            type="mac_address"
            fullWidth
            name="device_id"
            onChange={handleDeviceIdChange}
          />

          <FormControl className={classes.formControl}>
                  <InputLabel id="demo-simple-select-label">Select Model</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"

                    onChange={handleChange}
                  >
                    {props.model && props.model.map((e) => <MenuItem value={e}>{e}</MenuItem>)}
                    
                    
                  </Select>
            </FormControl>

          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Device Mac Address"
            type="mac_address"
            name="mac_address"
            fullWidth
            onChange={handleDeviceIdChange}
          />

          
          
          
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleClose} color="primary" onClick={registerDevice}>
            Save
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}


