import React from 'react';
import Url from '../component';
import {createStore, applyMiddleware} from 'redux';
import combineReducer from '../reducer/index.js';

const customMiddleWare = store => next => action => {
    let url = window.location.href;
    let lastPartUrlArray = url.split('/');
    let lastPartUrl = lastPartUrlArray[lastPartUrlArray.length -1];
    
    next(action);
}

const store = createStore(combineReducer, applyMiddleware(customMiddleWare));
console.log(store.getState());

export default store;


