import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import styles from './site-list.css';
import DataTable from 'react-data-table-component';
import CustomizedBreadcrumbs from '../../components/breadcumb/breadcumb';
import SideBar from '../../components/sidebar/sidebar';
import actionJson from '../../action/action';
import Button from '@material-ui/core/Button';
import AddDeviceModal from '../../components/addDevices/addDevicesModal';
import EnhancedTable from '../../components/table/TableData';
import EditDeviceModal from '../../components/editDevices/editDeviceModal';
import CustomizedSnackbars from '../../components/snackbar/snackbar';



class DeviceListPage extends Component {
    constructor() {
        super();
        this.state = {
            sidebarClick: true,
            open: false,
            deviceList: [],
            columns: [{ "id": "name", "numeric": false, "disablePadding": true, "label": "Id" },
            { "id": "calories", "numeric": true, "disablePadding": false, "label": "Device Id" },
            { "id": "fat", "numeric": true, "disablePadding": false, "label": "Mac Address" },
            { "id": "carbs", "numeric": true, "disablePadding": false, "label": "Model" },
            { "id": "protein", "numeric": true, "disablePadding": false, "label": "Created on" }],
            model: [],
            device_data: {},
            delete_status: false

        }


    }




    componentDidMount() {
        this.loadModel().then((resp) => {
            console.log("Resp Data", resp.data);
            this.setState({ model: resp.data.models });
        }).catch((err) => {
            if (err) {
                alert("Sorry some internal error occured! Please try after some time");
            }
        });

        this.loadDevice();
    }

    loadDevice() {
        axios.get('/admin/deviceprofile/list').then((resp) => {
            this.setState({ deviceList: resp.data });
        }).catch((err) => {
            if (err) {
                alert("Sorry some internal error occured! Please try after some time");
            }
        });
    }

    loadModel() {

        return axios({ method: 'get', url: '/admin/deviceprofile/model/list' })
    }

    toggleSidebar() {

        this.setState({ sidebarClick: !this.state.sidebarClick });
    }

    openAddDeviceModal(id) {

        axios.get('/admin/deviceprofile/' + id).then((resp) => {
            this.loadModel().then((re) => {
                console.log("re.data.model", re.data.models);
                this.setState({ open: true, device_data: resp.data, modal: re.data.models });
            })

        }).catch((err) => {

        });

    }

    closeEditModal() {
        this.setState({ open: false });
    }

    deleteDevice() {
        this.setState({ delete_status: true });
        this.loadDevice();
        setTimeout(() => {
            this.setState({ delete_status: false });
        }, 1000)
    }



    render() {



        let inlineStyle = {
            paddingLeft: '0px'
        }

        if (this.state.sidebarClick) {
            inlineStyle.paddingLeft = '300px'
        } else {
            inlineStyle.paddingLeft = '0px'
        }

        let site_list = [];


        return (
            <div>
                <CustomizedSnackbars open={this.state.delete_status} type="error" message="Device deleted successfully" />
                <SideBar sidebarClick={this.state.sidebarClick} onClick={this.toggleSidebar.bind(this)} />
                <main className={styles.pageContent} style={inlineStyle}>
                    <div className="container-fluid">

                        <AddDeviceModal
                            isOpen={this.state.open}
                            model={this.state.model}
                            load_device={this.loadDevice.bind(this)} />

                        <EditDeviceModal
                            open={this.state.open}
                            model={this.state.modal}
                            device_data={this.state.device_data}
                            closeEditModal={this.closeEditModal.bind(this)} />

                        <EnhancedTable
                            heading={'Device List'}
                            model={this.state.model}
                            columns={this.state.columns}
                            data={this.state.deviceList}
                            editModel={this.openAddDeviceModal.bind(this)}
                            load_device={this.deleteDevice.bind(this)}
                        />
                    </div>
                </main>
            </div>


        )
    }



}


const mapStateToProps = state => {
    return state;
}


export default connect(mapStateToProps)(DeviceListPage);


