import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import styles from '../DeviceListPage/site-list.css';
import DataTable from 'react-data-table-component';
import CustomizedBreadcrumbs from '../../components/breadcumb/breadcumb';
import SideBar from '../../components/sidebar/sidebar';
import actionJson from '../../action/action';
import Button from '@material-ui/core/Button';
import AddUserModal from '../../components/addUser/addUser';
import EnhancedTable from '../../components/table/TableData';
import UserTable from '../../components/user-table/UserTable';
import AddDeviceModal from '../../components/addDevice/AddDeviceModal';




class UserListPage extends Component {
    constructor() {
        super();
        this.state = {
            sidebarClick: true,
            open: false,
            viewModal: false,
            users: [],
            columns: [{ "id": "name", "numeric": false, "disablePadding": true, "label": "User Id" },
            { "id": "calories", "numeric": true, "disablePadding": false, "label": "Username" },
            { "id": "fat", "numeric": true, "disablePadding": false, "label": "Email" },
            { "id": "carbs", "numeric": true, "disablePadding": false, "label": "Mobile No." }],
            devices: [],
            deviceList: [],
            selectedUserId: 0,
            userDeviceData: []
            
            
        }
       
    }


    componentDidMount() {
        this.loadUserList();
        this.getAllDeviceList();
    }

    getDeviceInfo(id, type) {
        
           axios.get('/user/deviceprofile/idlist?userId='+id).then((resp) => {
              
                this.setState({open: true, selectedUserId: id, viewModal: true, userDeviceData: resp.data.devices}); 
           }).catch((err) => {
                this.setState({open: true})
           });
    }

    loadUserList() {
        axios.get('/airpurifer/admin/consumeruser/list').then((resp) => {
                this.setState({users: resp.data});
          }).catch((err) => {
                
          })
    }

    getAllDeviceList() {
        axios({ method: 'get', url: '/admin/deviceprofile/list' }).then((resp) => {
            console.log("Device List received", resp.data);
            this.setState({ deviceList: resp.data });
        }).catch((err) => {
            if (err) {
                alert("Sorry some internal error occured! Please try after some time");
            }
        });

    }

    closeDeviceModal() {
        this.setState({open: false});
    }

    toggleSidebar() {

        this.setState({ sidebarClick: !this.state.sidebarClick });
    }

    

    render() {



        let inlineStyle = {
            paddingLeft: '0px'
        }

        if (this.state.sidebarClick) {
            inlineStyle.paddingLeft = '300px'
        } else {
            inlineStyle.paddingLeft = '0px'
        }

        let site_list = [];


        return (
            <div>
                {/* <ListView open={this.state.viewModal} device_data={this.state.userDeviceData}/> */}
                <SideBar sidebarClick={this.state.sidebarClick} onClick={this.toggleSidebar.bind(this)} />
                <main className={styles.pageContent} style={inlineStyle}>
                    <div className="container-fluid">
                        <AddDeviceModal isOpen={this.state.open} 
                            closeDeviceModal={this.closeDeviceModal.bind(this)}
                            devices={this.state.deviceList}
                            user_id={this.state.selectedUserId}
                            device_data={this.state.userDeviceData} 
                            refresh_data={this.getDeviceInfo}/>
                        <UserTable columns={this.state.columns} data={this.state.users} deviceInfo={this.getDeviceInfo.bind(this)}/>

                        

                    </div>
                </main>
            </div>


        )
    }



}


const mapStateToProps = state => {
    console.log("state", state);
    return state;
}


export default connect(mapStateToProps)(UserListPage);


