const initialState = {
    menus: [
      
      {
        name: 'Device List',
        active: false,
        icons: 'fas fa-laptop',
        link: '/device-list',
        submenu: []
      },{
        name: 'Model List',
        active: false,
        icons: 'fas fa-laptop',
        link: '/model-list',
        submenu: []
      },{
        name: 'User List',
        active: false,
        icons: 'fas fa-user',
        link: '/user-list',
        submenu: []
      }
      
     ],
    
}

function rootReducer(state=initialState, action) {
    let url = window.location.href.split('/');
    let lastIndex = url[url.length -1];
   
    let mappedmenus = [...state.menus];
    
    let allMenus = [...state.menus];
    switch(action.type) {
        case 'TOGGLE_SUBMENU':
            
            
            allMenus.map((e, i) => {

                if (i == action.index) {
                  e.active = !e.active;
                } else {
                  e.active = false;
                }
                
            });
            
            state.menus = allMenus;
            break;
        case 'SELECT_SUBMENU':
            allMenus[action.index].submenu.map((submenu) => {
                if (submenu.link == '/'+lastIndex) {
                  submenu.active = true;
                } else {
                  submenu.active = false;
                }
                
            });
            break;

    }
    return state;
}

export default rootReducer;


