import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import Input from '@material-ui/core/Input';
import Checkbox from '@material-ui/core/Checkbox';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemText from '@material-ui/core/ListItemText';
import Axios from 'axios';
import { Table, TableBody, TableCell, TableHead, TableRow, TableContainer, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import CustomizedSnackbars from '../snackbar/snackbar';

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 400,
    maxWidth: 400,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  },
  noLabel: {
    marginTop: theme.spacing(3),
  },
}));

const names = [
  'Device 1',
  'Device 2',
  'Device 3',
  'Device 4'
];

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

export default function AddDeviceModal(props) {

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [deviceId, setDeviceId] = React.useState([]);
  const [device_label, setDeviceLabel] = React.useState('');
  const [status, setStatus] = React.useState(false);
  const [type, setType] = React.useState('success');
  const [message, setMessage] = React.useState('initial message');

  const handleChange = event => {
    setDeviceId(event.target.value);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
     props.closeDeviceModal();
  };

  const saveUserDevice = () => {
      let data = {label: device_label, deviceId: deviceId};
       Axios.post('/user/deviceprofile/register?userId='+props.user_id, data).then((response) =>{
          setStatus(true);
          setType('success');
          setMessage('Device added to user account successfully');
          props.closeDeviceModal();
          setTimeout(() => {setStatus(false);}, 2000)
       }).catch((err) => {

       });
  }

  const handleLabelChange = (event) => {
        setDeviceLabel(event.target.value);
  }

  const removeUserDevice = (id) =>{
      Axios.delete('/user/deviceprofile/'+id+'?userId='+props.user_id).then((resp) => {
        setStatus(true);
        setType('error');
        setMessage('Device deleted from user account successfully');
        props.closeDeviceModal();
      }).catch((err) => {

      });

      setTimeout(() => {setStatus(false);}, 2000)
  }; 

  return (
    <div>
      <CustomizedSnackbars open={status} type={type} message={message}/>
      <Dialog open={props.isOpen} onClose={handleClose} aria-labelledby="form-dialog-title">
        
        <DialogContent>
          <TableContainer>
          <Table>
            
            <TableHead>
              <TableRow>
                  <TableCell>Label</TableCell>
                  <TableCell>DeviceId</TableCell>
                  <TableCell>Action</TableCell>
              </TableRow>
                 
              </TableHead>
              <TableBody>
            {props.device_data && props.device_data.map((e) => {
              
               return (<TableRow key={e.deviceId}>
                      <TableCell component="th" scope="row">{e.label}</TableCell>
                      <TableCell>{e.deviceId}</TableCell>
                        <TableCell>
                          <IconButton>
                            <DeleteIcon onClick={removeUserDevice.bind(this, e.deviceId)}/>
                          </IconButton>
                        </TableCell>
                </TableRow>)
            })}
              
            </TableBody>
            
          </Table>
          </TableContainer>
          <DialogTitle id="form-dialog-title">Add Device</DialogTitle>
        <FormControl className={classes.formControl}>
        
        <FormControl>
        
        <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Label"
            type="mac_address"
            fullWidth
            name="device_id"
            onChange={handleLabelChange}
          />

        <FormControl className={classes.formControl}>
                  <InputLabel id="demo-simple-select-label">Select Device</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    onChange={handleChange}
                  >
                    {props.devices && props.devices.map((e) => <MenuItem key={e.deviceId} value={e.deviceId}>{e.deviceId}</MenuItem>)}
                  </Select>
            </FormControl>


        </FormControl>
        
        
      </FormControl>

        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleClose} color="primary" onClick={saveUserDevice.bind(this)}>
            Save
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}


