
import React from 'react';
import actionJson from '../action/action';
const initialState = {};

function deviceListReducer(state=initialState, action) {

    switch (action.type) {
        case actionJson.LOAD_SITE_LIST:
            state = action.data;
            
            break;
    }
    
    return state;
}

export default deviceListReducer;


