
import DeviceListPage from "./pages/DeviceListPage/DeviceListPage";
import LoginPage from "./pages/LoginPage/LoginPage";
import UserListPage from "./pages/UserListPage/UserListPage";
import ModelList from "./pages/modelList/ModelList";


var Url = [
    {
        url: '/',
        component: LoginPage,
        title: 'Login'
    },
    {
        url: '/login',
        component: LoginPage,
        title: 'Login'
    },
    {
        url: '/device-list',
        component: DeviceListPage,
        title: 'Device List'
    }, {
        url: '/user-list',
        component: UserListPage,
        title: 'User List'
    }, {
        url: '/model-list',
        component: ModelList,
        title: 'Model List'
    }
    
]

export default Url;


